import fs from 'fs';
import { promisify } from 'util';

const promisified = ['readFile', 'open', 'unlink'].reduce((acc, method) => {
    acc[method] = promisify(fs[method]);
    return acc;
}, {});

const fsUtils = {
    readFile(...args) {
        return promisified.readFile(...args);
    },

    remove(...args) {
        return promisified.unlink(...args);
    },

    canWrite(file) {
        return promisified.open(file, 'w');
    }
};

Reflect.set(fs, 'promise', fsUtils);

export default fs;
